// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
Vue.config.productionTip = false

import Vuex from 'vuex'
Vue.use(Vuex)
Vue.use(mavonEditor)

import main from './store'
const store = new Vuex.Store({
  modules: {
    main,
  }
});

import $ from 'jquery'
import {
  setTimeout
} from 'timers';

//var domain = 'http://localhost';
var domain = 'http://101.132.128.112:4000';
Vue.prototype.call = function (url, para, callback) {
  $.ajax({
    url: domain + url, //当前的RAMS
    //dataType: 'jsonp',
    data: para,
    type: 'POST',
    //jsonp: 'callback',
    beforeSend: function () {
      $('body').append('<div class="qwe" style="z-index:999;position:fixed;width:100vw;height:100vh;background:rgba(0,0,0,0.4);left:0px;top:0px;"><img style="width:200px;height:200px;position:absolute;left:50%;top:50%;transform:translate(-50%,-50%);" src="/static/Ripple.svg"/></div>')
    },
    success: function (rs) {
      $('.qwe').remove();
      callback(rs)
    },
    timeout: 30000
  });
}

Vue.prototype.getCookie = function (name) {
  var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if (arr = document.cookie.match(reg)) {
    return (arr[2]);
  } else {
    return null;
  }
}

Vue.prototype.setCookie = function (c_name, value, expiredays) {
  var exdate = new Date();
  exdate.setDate(exdate.getDate() + expiredays);
  document.cookie = c_name + "=" + escape(value) + ((expiredays == null) ? "" : ";expires=" + exdate.toGMTString());
};

Vue.prototype.delCookie = function (name) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null)
    document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {
    App
  }
})



