<template>
  <div>
    <div class="content" v-for="(item,i) in newsinfo" :key="i">
      <div class="left">
        <img :src="item.avatar_url" alt="" :title="item.html_url" @click="gotogithub(item.html_url)">
      </div>
      <div class="right">
        <div class="title">{{item.title}}</div>
        <div class="infoma">{{item.updatetime}} ✦ {{item.name}} ✦ {{item.email}}</div>
        <div class="con" v-html="item.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations
  } from 'vuex';

  export default {
    name: "name",
    computed: {
      ...mapState('main', {
        newsinfo: state => state.newsinfo
      })
    },
    created() {
      this.getnews();
    },
    data() {
      return {
        p: undefined
      };
    },
    methods: {
      ...mapMutations({
        getnews: 'main/getnews'
      }),
      gotogithub(url) {
        window.open(url);
      }
    }
  };

</script>

<style scoped>
  .con {
    padding: 6px;
    color: #ccc;
    font-size: 12px;
  }

  .infoma {
    font-size: 13px;
    padding: 3px 6px;
    border-bottom: 1px solid #555;
    color: #888;
  }

  .title {
    font-size: 20px;
    padding: 6px;
  }

  .left img:hover {
    border-radius: 10px !important;
  }

  .left img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.2s;
  }

  .right {
    overflow: hidden;
    width: 630px;
  }

  .left {
    float: left;
    width: 70px;
    height: 100%;
    overflow: hidden;
  }

  .content {
    overflow: hidden;
    background: rgba(0, 0, 0, 0.4);
    margin-bottom: 15px;
    border-radius: 2px;
    color: #fff;
    font-size: 15px;
    font-weight: 300;
  }

</style>



