import Vue from 'vue'
import Router from 'vue-router'

import Main from '../Main.vue'
import News from '../components/News.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Main',
    component: Main,
    children: [{
      path: '/News',
      name: 'News',
      component: News
    }]
  }]
})



