import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default {
  namespaced: true,
  state: {
    newsinfo: []
  },
  mutations: {
    getnews(state, para) {
      Vue.prototype.call('/getnews', {}, (rs) => {
        state.newsinfo = rs;
      })
    }
  }
}



