<template>
  <div class="content">
    <div class="top">
      <div class="topcontent">
        <div class="title">Creative6</div>
        <div class="fucbtn" v-for="(item,i) in option" @click="select(i)">{{item}}</div>
        <div class="trans" :style="{transform: 'translate3d(' + index*120 + 'px,0px,0px)'}"></div>
        <div class="searchbox">
          <input class="input" type="text" style="background: #444;" maxlength="20">
          <i class="iconfont icon-search1"></i>
        </div>
      </div>
    </div>

    <div class="down">
      <div class="mainbody">
        <div class="bodyleft">
          <div class="editorcontent" :class="editflg?'showe':''">
            <input class="input" type="text" placeholder="Title" v-model="title">
            <!-- <textarea name="" id="" cols="30" rows="10" placeholder="Content" v-model="content"></textarea> -->
            <div id="editorElem" style="text-align:left"></div>
            <!-- <mavon-editor v-model="value" default_open="edit" code_style='code-dark'/> -->

            <button class="button" @click="commit">Commit</button>
          </div>
          <router-view/>
        </div>
        <div class="bodyright">
          <div class="personinfo">
            <div v-if="userinfo">
              <div class="plist1">
                <img :src="userinfo.avatar_url" @click="gotogithub()" :title="userinfo.html_url">
                <div style="padding-top: 10px;">{{userinfo.name}}</div>
                <div style="font-size:13px;">{{userinfo.email}}</div>
                <div class="bio">{{userinfo.bio}}</div>
              </div>
              <div class="plist2" @click="editflg=!editflg">
                <i class="iconfont icon-edit"></i>
                <div>Add New</div>
              </div>
              <div class="plist2">
                <i class="iconfont icon-comments"></i>
                <div>My Msg</div>
              </div>
              <div class="plist2">
                <i class="iconfont icon-exit"></i>
                <div>Exit</div>
              </div>
            </div>
            <div v-else>
              <div class="plist2" @click="_login()">
                <i class="iconfont icon-github" style="font-size: 20px;"></i>
                <div>使用github登录</div>
              </div>
            </div>
          </div>
          <div class="tags">
            <div class="tag" v-for="(item,i) in classify" :key="i">
              <i class="iconfont icon-attachment"></i> {{item.name}}
              <div class="num">{{item.num}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="fot">
        Copyright © 2017 Flower
      </div>
    </div>

  </div>
</template>

<script>
  import {
    mapState,
    mapMutations
  } from 'vuex';


  import E from "wangeditor";
  import News from './components/News.vue'
  var editor;
  export default {
    name: 'name',
    components: {
      News,
    },
    data() {
      return {
        option: ['主页', '模块1', '模块2'],
        index: 0,
        userinfo: undefined,
        editflg: false,
        title: '',
        editorContent: '',
        value:'',
        classify: [{
            name: 'JAVA',
            num: 4
          },
          {
            name: 'c#',
            num: 2
          },
          {
            name: '数据库',
            num: 6
          },
          {
            name: 'c++',
            num: 3
          },
          {
            name: 'javascript',
            num: 4
          },
          {
            name: 'css',
            num: 1
          },
          {
            name: 'html',
            num: 8
          },
        ]
      }
    },
    created() {
      try {
        var userinfoString = unescape(this.getCookie("userinfo")).split('j:')[1];
        this.userinfo = JSON.parse(userinfoString);
      } catch (e) {
        console.log(e);
      }
    },
    methods: {
      ...mapMutations({
        getnews: 'main/getnews'
      }),
      select(i) {
        this.index = i;
      },
      _login() {
        window.location.href =
          'https://github.com/login/oauth/authorize?client_id=5264e5de73487b1b7a35&redirect_uri=http://101.132.128.112:4000/github';
      },
      gotogithub() {
        window.open(this.userinfo.html_url);
      },
      commit() {
        this.call('/savenews', {
          title: this.title,
          content: this.editorContent,
          uid: this.userinfo.id
        }, (rs) => {
          if (rs) {
            this.getnews();
            this.editflg = false;
            this.title = '';
            this.editorContent = '';
            editor.txt.html('')
          }
        })
      }
    },
    mounted() {
      this.$router.push('/News');

      editor = new E("#editorElem");
      //editor.customConfig.uploadImgServer = "http://www.creative6.cn:4000/qwe?uid=" + localStorage.getItem('uid');
      editor.customConfig.uploadImgShowBase64 = true;
      editor.customConfig.menus = [
        "head", // 标题
        "strikeThrough", // 删除线
        "quote", // 引用
        "emoticon", // 表情
        "table", // 表格
        "image",
        "code" // 插入代码
      ];
      editor.customConfig.onchange = html => {
        this.editorContent = html;
      };
      editor.create();
    }
  }

</script>

<style scoped>
  .num {
    float: right;
    color: #aaa;
  }

  .showe {
    height: 450px !important;
  }

  .editorcontent {
    width: 700px;
    background: rgba(0, 0, 0, 0.4);
    height: 0px;
    transition: height 0.2s;
    overflow: hidden;
    padding: 0px 20px;
  }

  .bio {
    font-size: 13px !important;
    width: 100%;
    padding: 0px 10px;
    line-height: 20px !important;
    margin-bottom: 10px;
  }

  .plist2 div {
    float: left;
    text-indent: 20px;
    font-size: 15px;
  }

  .plist2 i {
    float: left;
  }

  .plist2:hover {
    color: #fff;
  }

  .plist2 {
    overflow: hidden;
    padding: 10px 20px;
    border-top: 1px solid #555;
    cursor: pointer;
  }

  .plist1 span {
    font-size: 13px;
  }

  .plist1 div {
    line-height: 23px;
    float: left;
    text-indent: 20px;
    font-size: 20px;
  }

  .plist1 img:hover {
    border-radius: 10px;
  }

  .plist1 img {
    width: 50px;
    height: 50px;
    float: left;
    border-radius: 50px;
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    margin-bottom: 10px;
    transition: 0.2s;
  }

  .plist1 {
    overflow: hidden;
  }

  .personinfo {
    width: 100%;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.4);
    color: #b9b9b9;
    border-radius: 2px;
  }

  .tag:hover {
    background: rgba(0, 0, 0, 0.7);
    color: #ccc !important;
  }

  .tag {
    background: rgba(0, 0, 0, 0.4);
    padding: 5px 10px;
    margin: 5px;
    border-radius: 10px;
    color: #bbb;
    font-size: 15px;
    overflow: hidden;
    transition: background 0.2s;
    cursor: pointer;
  }

  .tags {
    width: 100%;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.4);
    margin-top: 20px;
    border-radius: 2px;
  }

  .bodyright {
    float: right;
    width: 270px;
    overflow: hidden;
  }

  .bodyleft {
    width: 700px;
    float: left;
  }

  .mainbody {
    width: 1000px;
    margin: 0 auto;
    overflow: hidden;
  }

  .down {
    width: 100%;
    margin-top: 80px;
    float: left;
    margin-top: 80px;
  }

  .fot {
    width: 100%;
    background: rgba(0, 0, 0, 0.7);
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    margin-top: 30px;
  }

  .searchbox input {
    width: 230px !important;
  }

  .searchbox i:hover {
    color: #bbb;
  }

  .searchbox i {
    position: absolute;
    color: #888;
    right: 11px;
    top: 22px;
    transition: 0.2s;
    cursor: pointer;
  }

  .searchbox {
    float: right;
    width: 270px;
    height: 100%;
    position: relative;
  }

  .trans {
    position: absolute;
    width: 100px;
    height: 5px;
    left: 200px;
    top: 55px;
    background: #00aaaa;
    transition: transform 0.2s;
  }

  .fucbtn {
    width: 100px;
    height: 100%;
    line-height: 60px;
    text-align: center;
    color: #fff;
    float: left;
    margin-right: 20px;
    cursor: pointer;
  }

  .title {
    width: 200px;
    font-size: 30px;
    line-height: 60px;
    color: #fff;
    float: left;
  }

  .topcontent {
    width: 1000px;
    height: 60px;
    position: relative;
    margin: 0 auto;
  }

  .top {
    width: 100%;
    height: 60px;
    background: rgba(0, 0, 0, 0.7);
    position: fixed;
    z-index: 1;
  }

  .content {
    width: 100%;
    height: 100%;
    position: relative;
  }

</style>



